import React, { useState, useEffect } from 'react';
import { Routes, Route, useNavigate } from 'react-router-dom';
import './App.css';
import Home from './pages/Home';
import Lock from './pages/Lock';
import Presale from './pages/Presale';
import Game from './pages/Game';
import Profile from './pages/Profile';
import AboutUs from './pages/AboutUs';
import Stats from './pages/Stats';
import Help from './pages/Help';
import Whitepaper from './pages/Whitepaper';
import RoadMap from './pages/RoadMap';
import ConfessAndEarn from './pages/ConfessAndEarn';
import Good from './pages/Good';
import Login from './pages/Login';
import SignUp from './pages/SignUp';
import Skey from './pages/Skey';

// Import all images from your src/Images folder
import home1 from "./Images/home1.png";
import home1Portrait from "./Images/home1-portrait.png";
import signIn from "./Images/sign-in.png";
import signInPortrait from "./Images/sign-in-portrait.png";
import presale from "./Images/presale.png";
import presalePortrait from "./Images/presale-portrait.png";
import game from "./Images/game.png";
import gamePortrait from "./Images/game-portrait.png";
import profile from "./Images/profile.png";
import profilePortrait from "./Images/profile-portrait.png";
import aboutUs from "./Images/about-us.png";
import aboutUsPortrait from "./Images/about-us-portrait.png";
import stats from "./Images/stats.png";
import statsPortrait from "./Images/stats-portrait.png";
import helpImg from "./Images/help.png";
import helpPortrait from "./Images/help-portrait.png";
import whitepaper from "./Images/whitepaper.png";
import whitepaperPortrait from "./Images/whitepaper-portrait.png";
import roadmap from "./Images/roadmap.png";
import roadmapPortrait from "./Images/roadmap-portrait.png";
import confeseAndEarn from "./Images/confese-and-earn.png";
import confeseAndEarnPortrait from "./Images/confese-and-earn-portrait.png";
import home2 from "./Images/home2.png";
import home2Portrait from "./Images/home2-portrait.png";
import loginImg from "./Images/login.png";
import loginPortrait from "./Images/login-portrait.png";

function App() {
  const navigate = useNavigate();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [isLocked, setIsLocked] = useState(false);
  const [isPortrait, setIsPortrait] = useState(window.innerHeight > window.innerWidth);

  // Detect login status and screen orientation
  useEffect(() => {
    // Check if the user is logged in
    const userStatus = localStorage.getItem('isLoggedIn');
    setIsLoggedIn(userStatus === 'true');

    // Orientation change listener
    const handleResize = () => {
      setIsPortrait(window.innerHeight > window.innerWidth);
    };

    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  const handleNavigation = (path) => {
    if (!isLoggedIn && path !== '/login' && path !== '/signup') {
      navigate('/login');
    } else {
      navigate(path);
    }
  };

  const handleLogout = () => {
    localStorage.removeItem('isLoggedIn');
    setIsLoggedIn(false);
    navigate('/login');
  };

  const handleLockToggle = () => {
    setIsLocked((prevState) => !prevState);
  };

  return (
    <div className="App">
      <div className="circle-buttons-container">
        {/* Navigation Buttons */}
        <button
          className="circle-button-home"
          onClick={() => handleNavigation("/home")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? home1Portrait : home1}
            alt="Home"
            className="circle-icon"
          />
        </button>
        <button className="circle-button-lock" onClick={handleLockToggle}>
          <img
            src={isPortrait ? signInPortrait : signIn}
            alt={isLocked ? "Unlock" : "Lock"}
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-shopping-cart"
          onClick={() => handleNavigation("/presale")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? presalePortrait : presale}
            alt="Presale"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-game"
          onClick={() => handleNavigation("/game")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? gamePortrait : game}
            alt="Game"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-head"
          onClick={() => handleNavigation("/profile")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? profilePortrait : profile}
            alt="Profile"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-dollar"
          onClick={() => handleNavigation("/about-us")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? aboutUsPortrait : aboutUs}
            alt="About Us"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-check"
          onClick={() => handleNavigation("/stats")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? statsPortrait : stats}
            alt="Stats"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-help"
          onClick={() => handleNavigation("/help")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? helpPortrait : helpImg}
            alt="Help"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-file"
          onClick={() => handleNavigation("/whitepaper")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? whitepaperPortrait : whitepaper}
            alt="Whitepaper"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-map"
          onClick={() => handleNavigation("/roadmap")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? roadmapPortrait : roadmap}
            alt="Roadmap"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-rewards"
          onClick={() => handleNavigation("/confess-and-earn")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? confeseAndEarnPortrait : confeseAndEarn}
            alt="Rewards"
            className="circle-icon"
          />
        </button>
        <button
          className="circle-button-learn"
          onClick={() => handleNavigation("/good")}
          disabled={isLocked}
        >
          <img
            src={isPortrait ? home2Portrait : home2}
            alt="Good"
            className="circle-icon"
          />
        </button>

        {/* Login/Logout Button */}
        {!isLoggedIn ? (
          <button
            className="circle-button-login"
            onClick={() => navigate("/login")}
          >
            <img
              src={isPortrait ? loginPortrait : loginImg}
              alt="Login"
              className="circle-icon"
            />
          </button>
        ) : (
          <button className="circle-button-logout" onClick={handleLogout}>
            <img
              src={isPortrait ? loginPortrait : loginImg}
              alt="Logout"
              className="circle-icon"
            />
          </button>
        )}
      </div>

      {/* Page Routes */}
      <Routes>
        <Route path="/home" element={<Home />} />
        <Route path="/lock" element={<Lock />} />
        <Route path="/presale" element={<Presale />} />
        <Route path="/game" element={<Game />} />
        <Route path="/profile" element={<Profile />} />
        <Route path="/about-us" element={<AboutUs />} />
        <Route path="/stats" element={<Stats />} />
        <Route path="/help" element={<Help />} />
        <Route path="/whitepaper" element={<Whitepaper />} />
        <Route path="/roadmap" element={<RoadMap />} />
        <Route path="/confess-and-earn" element={<ConfessAndEarn />} />
        <Route path="/good" element={<Good />} />
        <Route path="/login" element={<Login />} />
        <Route path="/signup" element={<SignUp />} />
        <Route path="/skey" element={<Skey />} />
      </Routes>
    </div>
  );
}

export default App;
